import React, { useContext, useRef, useEffect } from "react"
import { GlobalContext } from '../context/GlobalState'

import Experience from "../components/experience.js"

import sylloIcon from "../images/syllo.png"
import microsoftIcon from "../images/msft.png"
import mastercardIcon from "../images/mastercard.png"
import fourOneTwoIcon from "../images/412.jpg"
import isIcon from "../images/cmuInfoSys.png"
import roboticsIcon from "../images/cmuRobotics.png"
import btgIcon from "../images/btg.png"

const Experiences = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const modeClasses = onDarkMode ? "dark-mode-background dark-mode-outline" : "light-mode-background light-mode-outline";
  const scrollContainerRef = useRef(null);
  const scrollingActive = useRef(true);

  const today = new Date();

  const experiences = [
    {
      link: "https://www.microsoft.com/en-us/security/business/microsoft-entra",
      image: microsoftIcon,
      name: "Microsoft",
      position: "Product Management",
      details: "Authentication Experiences",
      duration: `${
        Math.round((((today.getFullYear() - 2020) * 12) + ((today.getMonth() + 1) - 8) - (today.getDate() < 17 ? 1 : 0)) / 12 * 100) / 100
        } Years`
    },
    {
      link: "https://syllo.ai",
      image: sylloIcon,
      name: "Syllo",
      position: "Software Engineering",
      details: "Syllo",
      duration: "7 Months"
    },
    {
      link: "https://azure.microsoft.com/en-au/product-categories/identity/",
      image: microsoftIcon,
      name: "Microsoft",
      position: "Product Management",
      details: "Customer Loyalty",
      duration: "3 Months"
    },
    {
      link: "https://www.mastercardservices.com/",
      image: mastercardIcon,
      name: "Mastercard",
      position: "Software Engineering",
      details: "Commercial Payments",
      duration: "2.5 Months"
    },
    {
      link: "https://412foodrescue.org/programs/foodrescuehero/",
      image: fourOneTwoIcon,
      name: "412 Food Rescue",
      position: "Software Engineering",
      details: "Food Rescue & Delivery",
      duration: "5 Months"
    },
    {
      link: "https://67272.cmuis.net/",
      image: isIcon,
      name: "67-272 Application Design & Development",
      position: "Head Teaching Assistant",
      details: "Web App Development",
      duration: "1 Semester"
    },
    {
      link: "https://cmubtg.com/",
      image: btgIcon,
      name: "CMUBTG",
      position: "President",
      details: "CMUBTG",
      duration: "2 Semesters"
    },
    {
      link: "https://www.autonlab.org/",
      image: roboticsIcon,
      name: "The Auton Lab",
      position: "Research Assistant",
      details: "The Auton Lab",
      duration: "1 Semester"
    },
  ];

  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    let scrollAmount = 0;
    let scrollDirection = 1; // 1 for right, -1 for left
    const scrollSpeed = 0.5; // Pixels per frame
    let scrollAnimationFrame = null;
    
    // Auto-scrolling animation
    const autoScroll = () => {
      if (!scrollContainer || !scrollingActive.current) return;
      
      scrollAmount += scrollSpeed * scrollDirection;
      scrollContainer.scrollLeft = scrollAmount;
      
      // Change direction when reaching the end
      if (scrollAmount >= scrollContainer.scrollWidth - scrollContainer.clientWidth) {
        scrollDirection = -1;
      } else if (scrollAmount <= 0) {
        scrollDirection = 1;
      }
      
      scrollAnimationFrame = requestAnimationFrame(autoScroll);
    };
    
    // Start auto-scrolling after a 2-second delay
    const scrollingTimer = setTimeout(() => {
      scrollAnimationFrame = requestAnimationFrame(autoScroll);
    }, 2750);
    
    // Pause scrolling when user interacts with the container
    const handleMouseEnter = () => {
      scrollingActive.current = false;
      scrollAmount = scrollContainer.scrollLeft;
      
      // Cancel any existing animation frame
      if (scrollAnimationFrame) {
        cancelAnimationFrame(scrollAnimationFrame);
        scrollAnimationFrame = null;
      }
    };
    
    // Resume scrolling when mouse leaves the container
    const handleMouseLeave = () => {
      scrollingActive.current = true;
      scrollAnimationFrame = requestAnimationFrame(autoScroll);
    };
    
    // Handle touch and wheel interactions
    const handleOtherInteractions = () => {
      scrollingActive.current = false;
      scrollAmount = scrollContainer.scrollLeft;
      
      // Cancel any existing animation frame
      if (scrollAnimationFrame) {
        cancelAnimationFrame(scrollAnimationFrame);
        scrollAnimationFrame = null;
      }
    };
    
    // Add interaction event listeners
    scrollContainer.addEventListener('mouseenter', handleMouseEnter);
    scrollContainer.addEventListener('mouseleave', handleMouseLeave);
    scrollContainer.addEventListener('touchstart', handleOtherInteractions);
    scrollContainer.addEventListener('touchend', handleMouseLeave);
    scrollContainer.addEventListener('wheel', handleOtherInteractions);
    
    return () => {
      // Clean up event listeners
      scrollContainer.removeEventListener('mouseenter', handleMouseEnter);
      scrollContainer.removeEventListener('mouseleave', handleMouseLeave);
      scrollContainer.removeEventListener('touchstart', handleOtherInteractions);
      scrollContainer.removeEventListener('touchend', handleMouseLeave);
      scrollContainer.removeEventListener('wheel', handleOtherInteractions);
      clearTimeout(scrollingTimer);
      if (scrollAnimationFrame) {
        cancelAnimationFrame(scrollAnimationFrame);
      }
    };
  }, []);

  return (
    <div className="experiences-container">
      <div className={`experiences ${modeClasses}`}>
        <div className="experiences-table-container auto-scrolling" ref={scrollContainerRef}>
          <table>
            <tbody>
              <tr>
                {experiences.map((experience, index) => {
                  return (
                    <td key={index}>
                      <Experience
                        orgURL={experience.link}
                        orgImage={experience.image}
                        orgName={experience.name}
                        positionName={experience.position}
                        positionDetails={experience.details}
                        positionDuration={experience.duration}
                      />
                    </td>
                  )
                })}
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default Experiences;
