import React, { useContext } from "react"
import { GlobalContext } from '../context/GlobalState'

import NavBar from "../components/navbar.js"
import Overview from "../components/overview.js"
import Experiences from "../components/experiences.js"
import Projects from "../components/projects.js"
import Bitmoji from "./common/bitmoji.js"
import FlyingBitmoji from "../components/flyingBitmoji"
import Footer from "../components/common/footer"

const Landing = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const modeClasses = onDarkMode ? "dark-mode-settings" : "light-mode-settings";

  return (
    <section className={`${modeClasses} section-container`}>
      <FlyingBitmoji />
      <NavBar />
      <div className="container landing-container">
        <Overview />
        <Experiences />
        <Projects />
      </div>
      <Bitmoji context={GlobalContext} page="main" />
      <Footer context={GlobalContext} />
    </section>
  );
}

export default Landing;
