import React, { useEffect, useRef } from "react"
import "../styles/flyingBitmoji.scss"

import bitmojiFlying from "../images/bitmojiFlying.png"

const FlyingBitmoji = () => {
  const bitmojiRef = useRef(null);

  useEffect(() => {
    const bitmoji = bitmojiRef.current;
    
    // Get window width to calculate proper starting position
    const windowWidth = window.innerWidth;
    
    // Set initial position off-screen to the right
    bitmoji.style.left = `${windowWidth}px`; // Start off-screen to the right
    bitmoji.style.top = "120px"; // Start from near the top
    
    let position = windowWidth;
    let topPosition = 120;
    let velocity = -3.5; // Initial upward velocity (negative y is up)
    const gravity = 0.1; // Reduced gravity effect
    
    // Animation frame function
    const animate = () => {
      position -= 5; // Move left by 5px per frame (slightly faster horizontal movement)
      velocity += gravity; // Apply gravity
      topPosition += velocity; // Update vertical position based on velocity
      
      // Update position (use left instead of right to ensure proper movement)
      bitmoji.style.left = `${position}px`;
      bitmoji.style.top = `${topPosition}px`;
      
      // Continue animation until bitmoji flies off the left side of the screen
      if (position > -150) {
        requestAnimationFrame(animate);
      } else {
        // Animation complete, remove the element
        if (bitmoji.parentNode) {
          setTimeout(() => {
            bitmoji.parentNode.removeChild(bitmoji);
          }, 500);
        }
      }
    };
    
    // Start the animation after a slight delay to ensure the page has loaded
    setTimeout(() => {
      // Make the bitmoji visible right before animation starts
      bitmoji.style.display = 'block';
      requestAnimationFrame(animate);
    }, 2500);
  }, []);

  return (
    <div className="flying-bitmoji-container">
      <img
        ref={bitmojiRef}
        id="bitmoji-flying"
        src={bitmojiFlying}
        alt="Flying Bitmoji"
      />
    </div>
  );
}

export default FlyingBitmoji;